    finishedMessageSelector = (totalCount, correctCount) => {
        const percentage = (correctCount / totalCount) * 100;

        if (percentage >= 80) {
            return "1:    ?";
        } else if (percentage >= 60) {
            return "2:     !";
        } else if (percentage >= 40) {
            return "3:   ";
        } else if (percentage >= 20) {
            return "4:   ..";
        } else {
            return "5:   ?";
        }
    }
